.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.row {
  display: flex;
  align-items: center;
  margin-top: 100px;
}

.square {
  width: 30px;
  height: 30px;
  border-radius: 50px;
  background-color: #006D5B;
  margin-right: 20px;
  transition: transform 0.5s ease-in-out;
}

.animate {
  transform: translateY(-50px);
}
